<template>
  <div class="main filter">
    <div class="main-table">
      <div style="border:1px solid #ccc">
        <ul>
          <li style="width:10%">
            <el-select v-model='proj' @change="changeSelect">
              <template v-for="item in projData">
                <el-option :label="item.memo" :value="item.id" :key="item.id">{{item.memo}}</el-option>
              </template>
            </el-select>
          </li>
          <li style="width:62%">
            <ol>
              <li style="width: 370px;">模型名称</li>
              <li>历史成功率</li>
              <li>累计外呼</li>
              <li>权重</li>
              <!--<li>撞库返回数据</li>-->
              <!--<li>系统撞库时间</li>-->
            </ol>
          </li>
          <li style="width:24%;margin:10px;text-align:right">
            <el-button class="btn-small" v-show="del.allState" @click="batchRemove">批量删除</el-button>
            <el-button class="btn-small" v-show="del.state" @click="cancel">取消</el-button>
            <el-button class="btn-small del-small" v-show="del.state" @click="delArr">删除</el-button>
          </li>
        </ul>
        <ul v-for="(items,index) in list.data" :key="index">
          <li style="width:10%;height:40px;line-height:40px;">
            <el-checkbox v-show="del.state" name="model" @change="getallModel(items,index,$event)" v-model="items.models_id"></el-checkbox><span>{{items.prjid_name}}</span>
          </li>
          <li style="width:64%">
            <ol v-for="(item,index) in items.mdata" :key="index">
              <li>
                <p><el-checkbox v-show="del.state" style="float: left;" name="model" v-model="item.models_mid" @change="getModels(item,items,index,$event)"></el-checkbox>
                  <span :title="modelData2[item.mid]" class="model-span">{{modelData2[item.mid]}}</span></p>
              </li>
              <li>{{item.leads_rate}}%</li>
              <li>{{item.hcallout}}</li>
              <li><el-input v-model="item.rank"></el-input></li>
            </ol>
          </li>
          <li style="width:22%;margin:10px 0px;text-align:right">
            <!--<el-button class="btn-small" value="" >立即撞库</el-button>-->
            <el-button class="btn-small" value="" @click="addproj(items)">添加模型</el-button>
            <el-button class="btn-small" value="" @click="saveproj(items)">保存</el-button>
          </li>
        </ul>
      </div>
      <page-table show_size="true" v-bind:cur="search.page" v-bind:total_page="list.totalPage" v-bind:total_count="list.totalCount" @change="change_data"></page-table>
    </div>
    <el-dialog :visible.sync="dialog_add" title="请从下拉菜单中选择新增模型">
      <div>
        <ul class="dialog-ul">
          <li style="width: 430px; margin-left: 5px">模型名称</li>
          <li style="margin-left: 3px;">历史成功率</li>
          <li style="margin-left: 10px">累计外呼</li>
        </ul>
        <ol v-for="(item, index) in addlist">
          <li>
            <el-select class="dialog_projselect" style="width:100%" v-model='item.mid' @change="getMid(item.mid,index)">
              <template  v-for="(model,index) in modelData">
                <el-option :label="model.memo" :value="model.id" :key="model.id">{{model.memo}}</el-option>
              </template>
            </el-select>
          </li>
          <li>{{item.leads_rate}}%</li>
          <li>{{item.hcallout}}</li>
          <li style="cursor:pointer" v-if='addlist.length != "1"' @click='dellist(index)'>删除</li>
        </ol>
      </div>
      <el-button @click="addprojselect">添加模型</el-button>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialog_add=false">取消</el-button>
        <el-button type="primary" @click="save">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import pageTable from '@/components/pager'
import ajax from "../../config/ajax"
import {format} from '@/config/util'
import Qs from 'qs'
import Vue from 'vue'
export default {
  name: 'data-alignment',
  components:{pageTable},
  data () {
    return {
      search: {
        size: 20, // 每页数量
        page: 1 // 页码
      },
      list:{
         data:[],
         totalCount:0,
         totalPage:0
      },
      del:{               //批量删除相关参数
        datas:{},
        state:false,
        allState:true
      },
      area:[],
      modelData2:[],
      modelData: [], // 模型字典数组
      addlist: [], // 新增模型数据
      proj: '',
      datanum: 3,
      proj_num: 1,
      dialog_add: false,
      projData: [],
      hleadsrate:'',
      hcallout:'',
      mid:[],
      prjid_local:'',
      delarr:[]
    }
  },
  created(){
    this.modelData = JSON.parse(localStorage.getItem("model"));
    this.projData = JSON.parse(localStorage.getItem("projid"));
    this.proj = this.projData[0].id;
    this.modelData2 = JSON.parse(localStorage.getItem("curl"));
    this.getInitialList();
  },
  methods: {
    getInitialList() {
      this.del.state=false
      this.del.allState=true
      this.delarr=[]
      var param = {
        prjid_local: this.proj,
        size: this.search.size,
        page: this.search.page
      }
      return ajax.request({
        method: 'get',
        url: 'filter/all',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        params: param
      }).then(res => {
        // console.log(res.data.list);
        this.list.data = res.data.list;
        for (var j=0;j<this.list.data.length;j++){
          Vue.set(this.list.data[j],'models_id',false);
          for(let k=0;k<this.list.data[j].mdata.length;k++){
            Vue.set(this.list.data[j].mdata[k],'models_mid',false);
          }
          for (var i = 0;i<this.projData.length;i++){
            if (this.projData[i].id === this.list.data[j].prjid_local) {
              Vue.set(this.list.data[j],'prjid_name');
              this.list.data[j].prjid_name=this.projData[i].memo
            }
          }
         }
        // console.log(this.list.data)
        this.list.totalCount = res.data.totalCount;
        this.list.totalPage = res.data.totalPage;
      }).catch(error => {
        console.log(error)
      })
    },
    adjust(obj){        //调整模型顺序
      if(obj.adjust_type){
        obj.adjust_type = false;
      }else{
        obj.adjust_type = true;
      }

    },
    addproj(val){ //添加模型弹框
      // console.log(val)
      this.dialog_add = true;
      this.prjid_local=val.prjid_local
      return ajax.request({
        method: 'get',
        url: 'area',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        params: {
          prjid_local: val.prjid_local,
        }
      }).then(res => {
        if (res.data.model.length === 0){
          this.area=this.modelData
        } else{
          this.modelData.shift();
          this.area = res.data.model;
          this.area=[];
          for (var i=0;i<res.data.model.length;i++){
            Object.keys(this.modelData2).map((key,item)=>{
              if (res.data.model[i] == key){
                this.area.push({
                  'memo':this.modelData2[key],
                  'id':res.data.model[i]
                })
              }
            });
          }
        }
        this.addlist=[]
          return ajax.request({
            method: 'get',
            url: 'count',
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
            },
            params: {
              prjid_local: val.prjid_local,
              mid: this.area[0].id
            }
          }).then(res => {
            this.hleadsrate=res.data[0].hleadsrate
            this.hcallout=res.data[0].hcallout
            var data = {
              mid: this.area[0].id,
              leads_rate: res.data[0].hleadsrate,
              hcallout: res.data[0].hcallout
            }
            this.addlist.push(data);
          }).catch((err)=>{
            console.log(err)
          })
      }).catch(error => {
        console.log(error)
      })
      this.getmodel(val);
    },
    //获取模型数据
    getmodel(list){
      var data = {
        mid: this.area[0].id,
        leads_rate: this.hleadsrate,
        hcallout: this.hcallout
      }
      this.addlist.push(data);
    },
    addprojselect(){
      this.getmodel()
    },
    dellist(index){
      this.addlist.splice(index,1);
    },
    save(){
      let mid=[]
      for (var i = 0;i<this.addlist.length;i++){
        mid.push(this.addlist[i].mid)
      }
       mid=mid.toString()
      return ajax.request({
        method: 'post',
        url: 'filter/add',
        data: {
          prjid_local: this.prjid_local,
          mid: mid
        }
      }).then(res => {
        this.$message.success('添加成功')
        this.dialog_add = false;
        this.getInitialList()
        // console.log(res)
      }).catch((err)=>{
        console.log(err)
        this.$message.error('添加失败')
      })
    },
    saveproj(list){
      let mdata = [];
      for(let i in list.mdata){
         let data = {};
          data.mid = list.mdata[i].mid,
          data.rank = list.mdata[i].rank
          mdata.push(data);
      }
      let params={
        prjid_local: list.prjid_local,
        mdata: mdata
      }
      return ajax.request({
        method: 'post',
        url: 'filter/update',
        data: params
      }).then(res => {
        if(res.code == '200'){
          this.$message.success("保存成功");
          this.getInitialList();
        }else{
          this.$message.error("保存失败");
        }
      }).catch(err => {
        console.log(err);
      })
    },
    change_data (index, size) {
      this.search.page = index;
      this.search.size = size;
      this.del.state = false;
      this.del.allState = true;
      this.delarr = [];
      this.getInitialList()
    },
    changeSelect(){
      this.getInitialList();
    },
    getMid(val,index){
      return ajax.request({
        method: 'get',
        url: 'count',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        params: {
          prjid_local: this.prjid_local,
          mid: val
        }
      }).then(res => {
        this.addlist[index].hcallout=res.data[0].hcallout
        this.addlist[index].leads_rate=res.data[0].hleadsrate
      }).catch((err)=>{
        console.log(err)
      })
    },
    getallModel(val,index,checked){
       // console.log(this.delarr)
        for(let i=0;i<this.list.data.length;i++){
          var obj = {};
          if(this.list.data[i].prjid_local==val.prjid_local){
            // Vue.set(this.list.data[i],'models_id',checked);
            obj.prjid_local = this.list.data[i].prjid_local;
            obj.mid = "";
            for(let j=0;j<this.list.data[i].mdata.length;j++){
              if (!checked){
                this.delarr.splice(i,1)
                // console.log(this.delarr)
                Vue.set(this.list.data[i].mdata[j],'models_mid',false);
              } else{
                Vue.set(this.list.data[i].mdata[j],'models_mid',checked);
                obj.mid += this.list.data[i].mdata[j].mid+",";
                this.delarr[i] = obj;
               }
              }
             var reg=/,$/gi;
             obj.mid=obj.mid.replace(reg,"");
          }
        }
    },
    getModels(val,vals,index,checked){
         for(let i=0;i<this.list.data.length;i++){
          if(this.list.data[i].prjid_local==vals.prjid_local){
            Vue.set(this.list.data[i],'models_id',false);
            Vue.set(this.list.data[i],'models_mid',false);
            // console.log(checked)
            if (!checked){
              let midarr=this.delarr[i].mid.split(',')
              for (let j=0;j<midarr.length;j++){
                // console.log(midarr[j])
                if (midarr[j] == val.mid){
                  midarr.splice(j,1)
                }
              }
              if (midarr.length == 0){
                this.delarr.splice(i,1)
              }else{
                this.delarr[i].mid = midarr.join(",");
              }
              // console.log(this.delarr)
            }else {
              if(this.delarr[i] == undefined){
                var obj ={};
                obj.prjid_local = vals.prjid_local;
                obj.mid = val.mid+"";
                this.delarr[i] = obj;
              }else{
                this.delarr[i].mid+=","+val.mid;
                if(this.delarr[i].mid.split(",").length == this.list.data[i].mdata.length){
                  Vue.set(this.list.data[i],'models_id',checked);
                }
              }
            }

          }
      }

    },
    batchRemove(){
      this.del.state=true
      this.del.allState=false
    },
    cancel(){
      this.del.state=false
      this.del.allState=true
    },
    //删除
    delArr() {
      if (this.delarr.length <=0){
        this.$message.info("请选择您要删除的文件");
      } else{
        return ajax.request({
          method: 'post',
          url: 'filter/delete',
          data: {
            mdata:this.delarr
          }
        }).then(res => {
          if(res.code == '200'){
            this.$message.success("删除成功");
            this.getInitialList();
          }else{
            this.$message.error("删除失败");
          }
        }).catch(err => {
          console.log(err);
        })
      }
   }
  },
  computed:{
    dragOptions() {
      return {
        animation: 500,
        group: "description",
        ghostClass: "ghost"
      };
    },
  },
  watch: {
    isDragging(newValue) {
      if (newValue) {
        this.delayedDragging = true;
        return;
      }
      this.$nextTick(() => {
        this.delayedDragging = false;
      });
    }
  }
}
</script>
<style>
   .el-select-dropdown__list{
    padding: 6px 6px !important;
  }
</style>
<style scoped>
  @import '../../assets/css/pages/main.css';
  .main-table{
    padding:15px;
  }
  .main .main-table ul{
    width:100%;
    overflow:hidden;
    border-bottom:1px solid #ccc;
  }
  .main .main-table ul:last-child{
    border-bottom:none;
  }
  .main .main-table ul li{
    float:left;
    padding:8px 0;
    margin-left:10px;
  }
  .main .main-table ul li .el-select .el-input{
    width:110px;
  }
  .main .main-table ol{
    margin-bottom: 8px;
    overflow:hidden;
  }
  .main .main-table ol li{
    padding:0!important;
    margin-left: 0px;
  }
  .main .main-table ol li p{
    height:30px;
    line-height:30px;
    margin:4px 0;
    padding-left:10px;
  }
  .main .main-table ol li.current p{
    border:1px solid #ccc;
  }
  .main .main-table ol li{
    line-height:40px;
  }
  .main .main-table ol li:first-child{
    overflow: hidden;
  }
  .model-span{
    display: inline-block;
    float: left;
    overflow: hidden;
    width: 365px;
  }

  .main .main-table ol li:nth-child(2){
    width:70px;
    text-align:center
  }
  .main .main-table ol li:nth-child(3){
    width:60px;
    text-align:center
  }
  .main .main-table ol li:nth-child(4){
    width:45px;
    text-align:center
  }
  .main .main-table ol li:nth-child(5){
    width:85px;
    text-align:center
  }
  .main .main-table ol li:first-child .el-select .el-input{
    width:260px;
  }
  .el-checkbox{
    margin-right:8px;
  }
  .main .main-table ol li{
    margin-right:10px;
  }
  .el-dialog ol{
    overflow:hidden;
  }
  .el-dialog ol li{
    float:left;
    line-height:40px;
    margin-right:10px;
    margin-bottom:8px;
  }
  .el-dialog ol li:first-child{
    width:60%;
    padding:0;
    border:none;
  }
  .el-dialog ol li {
    width: 65px;
    height: 42px;
    border-radius:5px;
    border:1px solid #ccc;
    text-align: center;
  }

  .flip-list-move {
    transition: transform 0.5s;
  }

  .no-move {
    transition: transform 0s;
  }

  .ghost {
    opacity: 0.5;
    background: #c8ebfb;
  }

  .list-group {
    min-height: 20px;
  }

  .list-group-item {
    cursor: move;
  }

  .list-group-item i {
    cursor: pointer;
  }
  .del-small{
    background-color: #007bc4;
    color: #fff;
  }
  .del-small:hover{
    background-color: #0f6196;
  }
  .dialog-ul li {
    display: inline-block;
    margin-bottom: 10px;
  }
</style>



